@font-face {
  font-family: 'RobotoRegular';
  src: url('../fonts/RobotoRegular.eot');
  src: url('../fonts/RobotoRegular.eot') format('embedded-opentype'), url('../fonts/RobotoRegular.woff2') format('woff2'), url('../fonts/RobotoRegular.woff') format('woff'), url('../fonts/RobotoRegular.ttf') format('truetype'), url('../fonts/RobotoRegular.svg#RobotoRegular') format('svg');
}

@font-face {
  font-family: 'RobotoBold';
  src: url('../fonts/RobotoBold.eot');
  src: url('../fonts/RobotoBold.eot') format('embedded-opentype'), url('../fonts/RobotoBold.woff2') format('woff2'), url('../fonts/RobotoBold.woff') format('woff'), url('../fonts/RobotoBold.ttf') format('truetype'), url('../fonts/RobotoBold.svg#RobotoBold') format('svg');
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

ul,
li {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

input {
  border: none;
}

button {
  background: none;
  border: none;
}

* {
  box-sizing: border-box;
}

.container {
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
  width: 100%;
}

body {
  font-family: 'RobotoRegular', 'Arial', sans-serif;
  line-height: 1.2;
  background-color: #e6e6e6;
  font-size: 15px;
}

.header_top--wrap {
  min-height: 35px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header_top--left {
  display: flex;
}

.header_top__item {
  margin: 0 10px;
  font-size: 13px;
}

.header_bottom {
  background-color: #fff;
}

.header_bottom--wrap {
  min-height: 90px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header_bottom--tel {
  position: relative;
}

.header_bottom--callback {
  padding: 17px 24px;
  text-align: center;
  background-color: #ff5400;
  font-size: 15px;
  color: #fff;
  border-radius: 5px;
  text-transform: uppercase;
}

.header_bottom__tel {
  font-family: 'RobotoBold', 'Arial', sans-serif;
  font-size: 18px;
}

.header_bottom__tel:hover {
  text-decoration: underline;
}

.header_bottom__callback {
  font-family: 'RobotoBold', 'Arial', sans-serif;
  display: block;
  margin-top: 5px;
  color: #ff5400;
  text-decoration: underline;
}

.header_bottom__li {
  margin: 15px 0;
  position: relative;
  padding-left: 35px;
}

.header_bottom__li p {
  color: #1f3a93;
}

.header_bottom__li::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
}

.header_bottom__li:nth-child(1)::after {
  background: url(../img/time.png) no-repeat;
  width: 24px;
  height: 24px;
  top: -3px;
}

.header_bottom__li:nth-child(2)::after {
  background: url(../img/car.png) no-repeat;
  width: 26px;
  height: 20px;
}

.intro_slider__title {
  font-family: 'RobotoBold', 'Arial', sans-serif;
  color: #fff;
  font-size: 30px;
}

.intro_slider--item {
  background: url(../img/intro.png) no-repeat;
  background-size: cover;
  background-position: center center;
  min-height: 437px;
}

.intro_slider__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 307px;
}

.intro_slider__desc {
  margin-top: 20px;
  max-width: 390px;
  font-size: 14px;
  color: #fff;
}

.intro_slider__flex {
  margin-top: 25px;
  max-width: 390px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.intro_slider__callback {
  padding: 17px 24px;
  text-align: center;
  background-color: #ff5400;
  font-size: 15px;
  color: #fff;
  border-radius: 5px;
  text-transform: uppercase;
  font-family: 'RobotoBold', 'Arial', sans-serif;
  text-transform: none;
  padding: 15px 30px;
}

.intro_slider__price {
  font-family: 'RobotoBold', 'Arial', sans-serif;
  color: #fff;
  text-decoration: underline;
}

.intro_slider__right {
  width: 200px;
  height: 200px;
  border-radius: 100px;
  background-color: #ff5400;
  text-align: center;
}

.intro_slider__icon {
  margin: 30px auto 10px;
}

.intro_slider__span {
  color: #fff;
  font-size: 18px;
  font-family: 'RobotoBold', 'Arial', sans-serif;
}

.intro_service--wrap {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
}

.intro_service--item {
  display: flex;
  flex-direction: column;
  width: 200px;
  height: 250px;
  border-radius: 12px;
  background-color: #fff;
  text-align: center;
}

.intro_service--item:first-child {
  box-shadow: 3px 5px 20px 1px rgba(0, 0, 21, 0.35);
  background-color: #ff5400;
}

.intro_service--item:nth-child(n+6) {
  margin-top: 30px;
}

.intro_service--item.intro_service--last {
  width: 680px;
  background: url(../img/service8.png) no-repeat;
  color: #fff;
}

.intro_service__img {
  margin: 35px auto 0;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.intro_service__title {
  font-size: 18px;
  margin: 30px auto 15px;
}

.intro_service__title.first {
  color: #fff;
}

.intro_service__a {
  font-size: 14px;
  line-height: 1.4;
  text-decoration: underline;
  color: #ff5400;
}

.intro_service__a.first {
  color: #fff;
}