.header {
    &_top {
        &--wrap {
            min-height: 35px;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        &--left {
            display: flex;
        }
        &__item {
            margin: 0 10px;
            font-size: 13px;
        }
    }
    &_bottom {
        background-color: #fff;
        &--wrap {
            min-height: 90px;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        &--tel {
            position: relative;
        }
        &--callback {
            .btn();
        }
        &__tel {
            .bold();
            font-size: 18px;
            &:hover {
                text-decoration: underline;
            }
        }
        &__callback {
            .bold();
            display: block;
            margin-top: 5px;
            color: @orange;
            text-decoration: underline;
        }
        &__li {
            margin: 15px 0;
            position: relative;
            padding-left: 35px;
            p {
                color: @blue;
            }
            &::after {
                content: '';
                position: absolute;
                top: 0; left: 0;
            }
            &:nth-child(1)::after {
                background: url(../img/time.png) no-repeat;
                width: 24px;
                height: 24px;
                top: -3px;
            }
            &:nth-child(2)::after {
                background: url(../img/car.png) no-repeat;
                width: 26px;
                height: 20px;
            }
        }
    }
}