.intro_slider__title {
    .bold();
    color: #fff;
    font-size: 30px;
}

.intro {
    &_slider {
        &--item {
            background: url(../img/intro.png) no-repeat;
            background-size: cover;
            background-position: center center;
            min-height: 437px;
        }
        &__wrap {
            display: flex;
            justify-content: space-between;
            align-items: center;
            min-height: 307px;
        }
        &__title {
            
        }
        &__desc {
            margin-top: 20px;
            max-width: 390px;
            font-size: 14px;
            color: #fff;
        }
        &__flex {
            margin-top: 25px;
            max-width: 390px;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        &__callback {
            .btn();
            .bold();
            text-transform: none;
            padding: 15px 30px;
        }
        &__price {
            .bold();
            color: #fff;
            text-decoration: underline;
        }
        &__right {
            width: 200px;
            height: 200px;
            border-radius: 100px;
            background-color: #ff5400;
            text-align: center;
        }
        &__icon {
            margin: 30px auto 10px;
        }
        &__span {
            color: #fff;
            font-size: 18px;
            .bold();
        }
        
    }
    &_service {
        &--wrap {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            flex-wrap: wrap;
        }
        &--item {
            display: flex;
            flex-direction: column;
            width: 200px;
            height: 250px;
            border-radius: 12px;
            background-color: #fff;
            text-align: center;
            &:first-child {
                box-shadow: 3px 5px 20px 1px rgba(0, 0, 21, 0.35);
                background-color: #ff5400;
            }
            &:nth-child(n+6) {
                margin-top: 30px;
            }
            &.intro_service--last {
                width: 680px;
                background: url(../img/service8.png) no-repeat;
                color: #fff;
            }
        }
        &__img {
            margin: 35px auto 0;
            height: 70px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        &__title {
            font-size: 18px;
            margin: 30px auto 15px;
            &.first {
                color: #fff;
            }
        }
        &__a {
            font-size: 14px;
            line-height: 1.4;
            text-decoration: underline;
            color: @orange;
            &.first {
                color: #fff;
            }
        }
    }
}