* {
    box-sizing: border-box;
}
.container {
    max-width: 1200px;
    padding: 0 15px;
    margin: 0 auto;
    width: 100%;
}
body {
    font-family: 'RobotoRegular','Arial', sans-serif;
    line-height: 1.2;
    background-color: #e6e6e6;
    font-size: 15px;
}
@orange: #ff5400;
@blue: #1f3a93;

.bold() {
    font-family: 'RobotoBold','Arial', sans-serif;
}
.h2() {
    .bold();
    color: #000;
    font-size: 28px;
    margin-top: 25px;
    text-align: center;
}

.btn() {
    padding: 17px 24px;
    text-align: center;
    background-color: @orange;
    font-size: 15px;
    color: #fff;
    border-radius: 5px;
    text-transform: uppercase;
}